import React from "react";
import "../../assets/css/About/rawbio1.css";
import rawimg from "../../assets/images/rawbioimg1.svg";

function Rawbiology() {
  return (
    <div className="mainraw">
      <div className="raw">
        <p>Raw Biology</p>
      </div>
      <div className="bio1-description-container">
        <div className="rawimg">
          <img src={rawimg} alt="rawimg" />
        </div>
        <div className="rawdescription">
          <p>
            We were born from the belief that there is no one size fits all and
            that each person deserves a product just for them, tailored to their
            hair, skin types, goals, and preferences. Because we are rule
            breakers, scientists, data nerds and engineers at heart, we just
            decided to create it. Ayurblends was born.
          </p>
        </div>
      </div>
    </div>
  );
}

export default Rawbiology;
