import React from 'react'
import "../../assets/css/payment/informationtab.css"

function Informationtab() {
    return (
        <div className='information-tab'>
            <p className='shipping-address-title'>Shipping Address</p>
            <form>
                <div className='payment-page-input-box'>
                    <input className='country-region' placeholder='Country/Region'></input>
                    <div>
                        <input className='first-name' placeholder='First Name(Optional)'></input>
                        <input className='last-name' placeholder='Last Name'></input>
                    </div>
                    <input className='Address' placeholder='Address(House/Flat number)'></input>
                    <input className='Apartment' placeholder='Apartment, Suite, etc.'></input>
                    <div>
                        <input className='Street' placeholder='Street Name'></input>
                        <input className='Landmark' placeholder='Landmark(Optional)'></input>
                    </div>
                    <span>
                        <input className='City' placeholder='City'></input>
                        <input className='State' placeholder='State'></input>
                        <input className='Zip' placeholder='ZIP Code'></input>
                    </span>
                    <input className='Phone' placeholder='Phone Number'></input>
                </div>
                <input className='continue-shipping1'></input>
            </form>
        </div>
    )
}

export default Informationtab