import React, { useState } from 'react'
import Navbar from "../NavBar";
import FooterComponent from "../FooterComponent";
import "../../assets/css/payment/payment.css"
import Paymenttab from './Paymenttab';
import Shippingtab from './Shippingtab';
import Informationtab from './Informationtab';
import SideCart from './SideCart';
import { useNavigate } from 'react-router-dom';



function Payment() {
    const navigate = useNavigate()


    const [information, setinformation] = useState(true)
    const [shipping, setshipping] = useState()
    const [payment, setpayment] = useState()
    const [infotab, setinfotab] = useState("payment-tab-active")
    const [paytab, setpaytab] = useState("payment-tab")
    const [shiptab, setshiptab] = useState("payment-tab")


    const handleinformation = () => (
        setpayment(false),
        setinformation(true),
        setshipping(false),
        setinfotab("payment-tab-active"),
        setshiptab("payment-tab"),
        setpaytab("payment-tab")
    )
    const handleshipping = () => (
        setpayment(false),
        setinformation(false),
        setshipping(true),
        setpaytab("payment-tab"),
        setshiptab("payment-tab-active"),
        setinfotab("payment-tab")
    )
    const handlepayment = () => (
        setpayment(true),
        setinformation(false),
        setshipping(false),
        setshiptab("payment-tab"),
        setinfotab("payment-tab"),
        setpaytab("payment-tab-active")
    )
    return (
        <div className='payment-page'>
            <Navbar></Navbar>
            <div class="payment-page-parent">
                <div class="payment-page-div1">
                    <div>
                        <p className='payment-title'>Payment</p>
                        <p className='payment-caption'>Forgot something to add in the cart?<u><b>Continue Shopping</b></u></p>
                        <div className='payment-tabs'>
                            <div className={infotab}>Information</div>
                            <div>&#62;</div>
                            <div className={shiptab}>
                                Shipping</div>
                            <div>&#62;</div>
                            <div className={paytab}>
                                Payment</div>
                        </div>
                    </div>
                    {information ? (
                        <div>
                            <Informationtab></Informationtab>
                            <button onClick={() => { handleshipping() }} className='continue-shipping'>Continue to Shipping</button>
                        </div>

                    ) : null}

                    {shipping ? (
                        <div>
                            <Shippingtab></Shippingtab>
                            <div onClick={() => { handleinformation() }} className='back-to-information'>&#60; Back to Information</div>
                            <button onClick={() => { handlepayment() }} className='pay-continue-btn'>Continue to Payment </button>
                        </div>
                    ) : null}

                    {payment ? (
                        <div style={{ display: 'flex', flexDirection: "column" }}>
                            <Paymenttab></Paymenttab>
                            <div onClick={() => { handleshipping() }} className='back-to-information'>&#60; Back to Shipping</div>
                            <button onClick={()=>{navigate("/ShopAll")}} className='pay-now-btn' >Pay Now</button>
                        </div>
                    ) : null}
                </div>
                <div class="payment-page-div2">
                    <SideCart></SideCart>
                </div>
            </div>
            <FooterComponent></FooterComponent>
        </div >
    )

}

export default Payment;