import React from "react";
import "../../../assets/1mobilecss/homepage/mobileconcerns.css";
import blemishes from "../../../assets/images/blemishes.svg";
import unevenskin from "../../../assets/images/unevenskin.svg";
import dryskin from "../../../assets/images/dryskin.svg";
import frizzyhair from "../../../assets/images/frizzyhair.svg";

export default function MobileConcerns() {
  return (
    <div className="m-concerns">
      <p className="m-concerns-title">Shop by Concerns</p>
      {/* <div> */}
        <div className="m-concerns-box">
          <div className="m-concerns-div">
            <img src={blemishes} alt="" />
            <p>Blemishes</p>
          </div>
          <div className="m-concerns-div">
            <img src={unevenskin} alt="" />
            <p>Uneven Skin</p>
          </div>
      </div>

        {/* </div> */}
        <div className="m-concerns-box">
          <div className="m-concerns-div">
            <img src={dryskin} alt="" />
            <p>Dry Skin</p>
          </div>
          <div className="m-concerns-div">
            <img src={frizzyhair} alt="" />
            <p>Dry Skin</p>
          </div>
        </div>
    </div>
  );
}
