import React from 'react'
import "../../assets/css/payment/paymenttab.css"

function Paymenttab() {
    return (
        <div>
            <form>
                <p className='payment-method'>Payment Method &nbsp;&nbsp;&nbsp;</p>
                <div className='paymenttab-input-box'>
                    <div className='standard-shipping payment-radio-input'>
                        <input type="radio" value="standard shipping" name="gender" />
                        <p>&nbsp;&nbsp;Credit Card </p>
                    </div>
                    <div className='cash-on-delivery payment-radio-input'>
                        <input type="radio" value="cash on delivery" name="gender" />
                        <p>&nbsp;&nbsp;UPI </p>
                    </div>
                    <div className='cash-on-delivery payment-radio-input'>
                        <input type="radio" value="cash on delivery" name="gender" />
                        <p>&nbsp;&nbsp;Debit Card </p>
                    </div>
                    <div className='cash-on-delivery payment-radio-input'>
                        <input type="radio" value="cash on delivery" name="gender" />
                        <p>&nbsp;&nbsp;Cash on Delivery </p>
                    </div>
                </div>
            </form>
        </div>
    )
}

export default Paymenttab