import React from "react";
import "../../assets/css/payment/orderreceipt.css";
import receiptimage from "../../assets/images/receiptimage.svg";
import orderconfirmedvector from "../../assets/images/orderconfirmedvector.svg";
import Checkbox from "@mui/material/Checkbox";

export default function OrderReceipt() {
  const [checked, setChecked] = React.useState([true, false]);

  return (
    <div className="receipt-container">
      <div className="receipt-header">
          <img className="receipt-backgroundimage" src={receiptimage} alt="" />
        <div className="receipt-main">
          <img src={orderconfirmedvector} alt="" />
          <p>Order Confirmed!</p>
        </div>
        <div className="receipt-caption">
          <p className="receipt-caption1">
            Your order has been confirmed and will be shipped right away!
          </p>
          <p className="receipt-caption2">
            Track your order <a href="/">here</a>
          </p>
        </div>
      </div>

      <div>
        <div className="receipt-item-quantity">
          <div>
            <p className="receipt-item">Item Ordered</p>
            <p className="receipt-item-name">ACT Face Cleanser | Dry skin</p>
          </div>
          <div>
            <p className="receipt-item">Quantity</p>
            <p className="receipt-item-name">1</p>
          </div>
        </div>

        <div className="receipt-item-quantity">
          <div>
            <p className="receipt-item">Order Date</p>
            <p className="receipt-item-name">3 Nov 2022</p>
          </div>
          <div>
            <p className="receipt-item">Order Number</p>
            <p className="receipt-item-name">#192920738910290</p>
          </div>
          <div>
            <p className="receipt-item">Payment Method</p>
            <p className="receipt-item-name">UPI</p>
          </div>
        </div>

        <div className="receipt-item-quantity-row3">
          <div>
            <p className="receipt-item">Delivery Address</p>
            <p className="receipt-item-name-row3">
              23 Gould Farm Street 52 Karwim New Jersey NJ
            </p>
          </div>
          <div>
            <p className="receipt-item">Est. Delivery Date</p>
            <p className="receipt-item-name">12 Nov 2022</p>
          </div>
        </div>

        <div className="receipt-whatsapp">
          <Checkbox checked={checked[0] && checked[1]} />
          <p>Send updates to Whatsapp</p>
        </div>
        <div className="receipt-download">
          <p>Download Receipt</p>
        </div>
      </div>
    </div>
  );
}
