import React from "react";
import "../../assets/css/ProductList/productlist.css";
import ProductGrid from "./FaceGrid";
import YouMightAlsoLike from "./YouMightAlsoLike";
import Productfilter from "./Productfilter";
import NavBar from "../NavBar";
import FooterComponent from "../FooterComponent";
import WooCommerceRestApi from "@woocommerce/woocommerce-rest-api";

function Face() {
  return (
    <div>
      <NavBar></NavBar>
      <div className="product-title-component">
        <div className="shop-all">
          <p>Face</p>
        </div>
        <div className="shop-all-description">
          <p>
            We use the safest natural ingredients, tried and tested gently to
            assure you the best care with zero side effects
          </p>
        </div>
      </div>
      <div class="productlist-parent">
        <div class="shop-all-productlist-div1">
          <Productfilter></Productfilter>
        </div>
        <div class="productlist-div2">
          <ProductGrid></ProductGrid>
        </div>
      </div>
      <YouMightAlsoLike></YouMightAlsoLike>
      <FooterComponent></FooterComponent>
    </div>
  );
}

export default Face;
