import React from 'react'
import '../../../assets/1mobilecss/aboutusmobile/howstartedmob.css'
import howstar from '../../../assets/images/howstarted.svg'

function Howwestartedmob() {
  return (
    <div className="howstartmobmain"> 

     <div className="howstartedmob-main"><p>How we started</p>
     <img className="howstartedmobpic" src={howstar}/>
     <div className="howstartedmob-caption">
     <p >Back in 2019, our first customers were only our friends and relatives to whom we regularly distributed our products for reviews before handing them out to you so that you can love, care and pamper yourself free from all toxicity. It is with their valuable feedback and your trust that Ayur Blends has been able to improve and show the best results.</p>
     </div>
     </div>
    </div>
  )
}

export default Howwestartedmob


