import React, { useState } from "react";
import "../../assets/css/ProductList/productgrid.css";
import productlistcart from "../../assets/images/productlistcart.svg";
import WooCommerceRestApi from "@woocommerce/woocommerce-rest-api";
import { useEffect } from "react";
import WooCommerce from "../../Api.js"

export default function ProductGrid() {
  const [Products, setProducts] = useState([]);
  useEffect(() => {
    fetchProducts();
  }, []);

  useEffect(() => {
    fetchProducts();
  }, []);

  let fetchProducts = () => {
    WooCommerce
      .get("products?category=17", {
        per_page: 20
      })
      .then((response) => {
        if (response) {
          console.log(response.data)
          setProducts(response.data);
        }
      })
      .catch((error) => { });
  };
  return (
    <div class="grid-section">
      <div className="grid-dropdown">
        <select>
          <option value="none" selected disabled hidden>
            SORT BY
          </option>
          <option value="Popularity">Popularity</option>
          <option value="Discount">Discount</option>
          <option value="Name">Name</option>
          <option value="Top Rate">Customer Top Rate</option>
          <option value="New Arrivals">New Arrivals</option>
          <option value="Low - High">Low - High</option>
          <option value="High - Low">High - Low</option>
        </select>
      </div>

      <div class="productgrid-container">
        <div class="grid-row">
          {Products.map((user) => (
            <div class="grid-productcard">
              <div class="grid-description">
                <div className="grid-productname-and-price">
                  <p className="grid-productname">{user.name}</p>
                  <p className="grid-productprice"> ₹ {user.price}</p>
                </div>
                <p className="grid-productrating">{user.rating_count}</p>
              </div>
              <div className="grid-image">
                <img className="grid-product-image" src={user.images[0].src} />
                <img src={productlistcart} className="grid-productlistcart" />
                {/* <p>{user.catergories[1].name}</p> */}
              </div>
            </div>
          ))}
        </div>
      </div>
      <div className="grid-button">
        {/* <button>Load More</button> */}
      </div>
    </div>
  );
}
