import React from "react";
import "../../assets/css/homepage/pickyourroutine.css";
import routineimage from '../../assets/images/routine.svg';
export default function PickYourRoutine() {
  return (
    <div className="routine-container">
      <div className="routine-main">
        <div className="routine-title">
          <p>Everyday is a new opportunity to heal.</p>
        </div>
        <div className="routine-line"></div>
      </div>
      <div className="routine-image">
        <div className="routine-image-text">
          <img src={routineimage}></img>
          <div className="image-text">
            <p className="image-text1">
              Decide your Day. Create your own Regime We want you to focus on
              yourself. Everyday is a new opportunity to heal.
            </p>
            <p className="image-text2">Pick your Routine</p>

            <button className="routine-button">Begin Quiz</button>
          </div>
        </div>

      </div>
    </div>
  );
}
