import React from "react";
import "../../assets/css/About/howwestarted.css";
import howstarted from "../../assets/images/howstarted.svg";

function Howstarted() {
  return (
    <div className="how-main">
      <div className="how">
        <p>How we started</p>
      </div>
      <div className="how-description">
        <div className="howdes">
          <p>
            Back in 2019, our first customers were only our friends and
            relatives to whom we regularly distributed our products for reviews
            before handing them out to you so that you can love, care and pamper
            yourself free from all toxicity. It is with their valuable feedback
            and your trust that Ayur Blends has been able to improve and show
            the best results.
          </p>
        </div>
        <div className="howimg">
          <img src={howstarted} alt="howstarted" />
        </div>
      </div>
    </div>
  );
};

export default Howstarted;
