import React from "react";
import "../../../assets/1mobilecss/homepage/certificationsmobile.css";
import organicmobile from "../../../assets/images/organicmobile.svg";
import animalsmobile from "../../../assets/images/animalsmobile.svg";
import naturalmobile from "../../../assets/images/naturalmobile.svg";
import crueltymobile from "../../../assets/images/crueltymobile.svg";

export default function () {
  return (
    <div className="m-certifications">
      <p className="m-certifications-title">Our Certifications</p>
      <p className="m-certifications-caption">So you trust us</p>
      <div className="m-certifications-box">
        <div
          style={{
            display: "flex",
            flexDirection: "row",
            justifyContent: "space-around",
          }}
        >
          <div className="m-certifications-item">
            <img src={organicmobile} alt="organic" />
            <p>Organic</p>
          </div>
          <div className="m-certifications-item">
            <img src={animalsmobile} alt="not tested on animals" />
            <p>Not tested on animals</p>
          </div>
        </div>
        <div
          style={{
            display: "flex",
            flexDirection: "row",
            justifyContent: "space-around",
          }}
        >
          <div className="m-certifications-item">
            <img src={naturalmobile} alt="100% natural" />
            <p>100% Natural</p>
          </div>
          <div className="m-certifications-item">
            <img src={crueltymobile} alt="cruelty free" />
            <p>Cruelty Free</p>
          </div>
        </div>
      </div>
    </div>
  );
}
