import React, { useState } from "react";
import "../assets/css/navbar.css";
import Logo from "../assets/images/logo.svg";
import search from "../assets/images/search.svg";
import cart from "../assets/images/cart.svg";
import profile from "../assets/images/profile.svg";
import menustack from "../assets/images/iconsmenu.png"
import closevector from "../assets/images/closevector.svg"
import { useNavigate } from "react-router";

function NavBar() {
  const navigate = useNavigate()

  const [navbar, setnavbar] = useState(false)
  const shownav = event => {
    setnavbar(current => !current);
  };

  return (
    <div className="navbar">
      <div className="text">
        FREE SHIPPING ON ALL AYURBLENDS PRODUCTS! FEB. 25–28.
      </div>
      <div className="menubaricon" onClick={shownav} >
        <img src={menustack}></img>
      </div>
      {navbar && (
        <div className="sidenav">
        <div className="menubar" onClick={shownav}>
          <div><img src={closevector}></img></div>
          <div>MENU</div>
        </div>
        <div className="menubarlist">
          <li>
            <a href="" onClick={() => {
              navigate("/ShopAll");
            }}>SHOP ALL</a>
          </li>
          <li>
            <a href="" onClick={() => {
              navigate("/Body");
            }}>BODY</a>
          </li>
          <li>
            <a href="" onClick={() => {
              navigate("/Hair");
            }}>HAIR</a>
          </li>
          <li>
            <a href="" onClick={() => {
              navigate("/Face");
            }}>FACE</a>
          </li>
          <li>
            <a href="" onClick={() => {
              navigate("/Blog");
            }}>BLOG</a>
          </li>
          <li>
            <a href="" onClick={() => {
              navigate("/FAQ");
            }}>FAQ</a>
          </li>
          <li>
            <a href="" onClick={() => {
              navigate("/About");
            }}>ABOUT</a>
          </li>
          <li>
            <a href="" onClick={() => {
              navigate("/Contactus");
            }}>CONTACT</a>
          </li>
        </div>
      </div>
      )}
      
      <div className="nav">
        <ul className="NavTabs">
          <li>
            <a href="" onClick={() => {
              navigate("/ShopAll");
            }}>SHOP ALL</a>
          </li>
          <li>
            <a href="" onClick={() => {
              navigate("/Body");
            }}>BODY</a>
          </li>
          <li>
            <a href="" onClick={() => {
              navigate("/Hair");
            }}>HAIR</a>
          </li>
          <li>
            <a href="" onClick={() => {
              navigate("/Face");
            }}>FACE</a>
          </li>
          <li>
            <a href="" onClick={() => {
              navigate("/Blog");
            }}>BLOG</a>
          </li>
          <li>
            <a href="" onClick={() => {
              navigate("/FAQ");
            }}>FAQ</a>
          </li>
          <li>
            <a href="" onClick={() => {
              navigate("/About");
            }}>ABOUT</a>
          </li>
          <li>
            <a href="" onClick={() => {
              navigate("/Contactus");
            }}>CONTACT</a>
          </li>
        </ul>
        <div className="logo" onClick={() => {
          navigate("/");
        }}>
          <img src={Logo} alt=""></img>
        </div>
        <div>
          <ul className="icons">
            <li>
              <img src={search} alt="" ></img>
            </li>
            <li>
              <img src={cart} alt=""></img>
            </li>
            <li>
              <img src={profile} onClick={() => {
                navigate("/myprofile")
              }} alt=""></img>
            </li>
          </ul>
        </div>
      </div>
    </div>
  );
}

export default NavBar;
