import React from "react";
import "../../assets/css/myprofile/myprofiletab.css";
import elipse from "../../assets/images/elipse.svg";
import editicon from "../../assets/images/editicon.svg";
import elipseminus from "../../assets/images/elipsminus.svg";
import verified from "../../assets/images/verified.png";

function MyprofileTab() {
  return (
    <div className="myprofileinfo-main">
      <div className="myprofileinfo-text-main">
        <p className="myprofileinfo-text">My Profile</p>
      </div>
      <div className="myprofile-grids">
        <div className="myprofile-elipse">
          <img src={elipse} alt="" />
        </div>
        <div>
          <div>
            <p className="myprofile-name">Jane Doe</p>
            <div className="myprofile-namediv">
              <p className="myprofile-dob">DOB : 12/12/1993</p>
              <p className="myprofile-edit">EDIT</p>
            </div>
            <div className="myprofile-line"></div>
            <p className="myprofile-email">Email</p>
            <div className="myprofile-emaildiv">
              <p className="myprofile-person-email">jane_2@gmail.com</p>
              <div className="myprofile-verified">
                <img src={verified} alt="" />
                <p style={{ margin: 0, color: "#1B8112" }}>Verified</p>
              </div>
            </div>
            <div className="myprofile-small-line"></div>
            <p className="myprofile-phoneno">Phone No.</p>
            <div className="myprofile-phonediv">
              <p className="myprofile-person-number"> +91-9090689685</p>
              <div className="myprofile-verified">
                <img src={verified} alt="" />
                <p style={{ margin: 0, color: "#1B8112" }}>Verified</p>
              </div>
            </div>
            <div className="myprofile-small-line"></div>
            <div className="address-main">
              <p className="saveadress">Save Address</p>
              <a href="/" className="addnew">
                ADD NEW
              </a>
            </div>
            <div className="myprofile-line"></div>
            <div className="address-container">
              <div className="place-edit">
                <div className="place-frame">
                  <p className="myprofile-place">Bangalore</p>
                </div>
                <div className="myprofile-icons">
                  <img className="myprofile-editicon" src={editicon} alt="" />
                  <img className="elipsmin" src={elipseminus} alt="" />
                </div>
              </div>
              <div>
                <p className="temporary-address">
                  213, O Block, Golden City, YeswanthpurPeenya - 560124
                </p>
                <p className="myprofile-phonenumber">+91-9090689685</p>
              </div>
              <div className="address-small-line"></div>
            </div>
            <div className="address-container">
              <div className="place-edit">
                <div className="place-frame">
                  <p className="myprofile-place">New Delhi</p>
                </div>
                <div className="myprofile-icons">
                  <img className="myprofile-editicon" src={editicon} alt="" />
                  <img className="elipsmin" src={elipseminus} alt="" />
                </div>
              </div>

              <div>
                <p className="permanant-address">
                  F 213, New Friends Colony Golden City, New Delhi 102000
                </p>
                <p className="myprofile-phonenumber">+91-9090689685</p>
              </div>
              <div className="address-small-line"></div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}

export default MyprofileTab;
