import React from "react";
import { useState, useRef } from "react";
import "../../assets/css/ProductDetail/productimage.css";
import slideimage1 from "../../assets/images/slideimage1.svg";
import slideimage2 from "../../assets/images/slideimage2.svg";
import slideimage3 from "../../assets/images/slideimage3.svg";
import slideimage4 from "../../assets/images/slideimage4.svg";

const images = [
  slideimage1, 
  slideimage2, 
  slideimage3, 
  slideimage4,
];
function ProductImage() {
  const [img, setImg] = useState(images[1]);
  const hoverHandler = (image, i) => {
    setImg(image);
    refs.current[i].classList.add("active");
    for (var j = 0; j < images.length; j++) {
      if (i !== j) {
        refs.current[j].classList.remove("active");
      }
    }
  };
  const refs = useRef([]);
  refs.current = [];
  const addRefs = (el) => {
    if (el && !refs.current.includes(el)) {
      refs.current.push(el);
    }
  };
  return (
    <div className="photoimage-container">
      <div className="photoimage-left">
        <div className="photoimage-left_1">
          {images.map((image, i) => (
            <div
              className={i == 0 ? "img_wrap active" : "img_wrap"}
              key={i}
              onMouseOver={() => hoverHandler(image, i)}
              ref={addRefs}
            >
              <img src={image} alt="" />
            </div>
          ))}
        </div>
        <div className="photoimage-left_2">
          <img src={img} alt="" />
        </div>
      </div>
      <div className="photoimage-right"></div>
    </div>
  );
}

export default ProductImage;
