import React from "react";
import "../../assets/css/payment/orderedproduct.css";
import facecleanser from "../../assets/images/facecleanser.svg";

export default function OrderedProduct() {
  return (
    <div className="orderedproduct-container">
      <p>Coming soon at your doorstep! 🎉</p>
      <div className="orderedproduct-title">
        <img src={facecleanser} alt="" />
        <div style={{display: 'flex', flexDirection: 'column', paddingLeft: 20, marginTop: 50}}>
        <p className="ordered-product-name">ACT Face Cleanser | Dry skin </p>
        <p className="ordered-product-quantity">Quantity : 1</p>

        </div>
      </div>
      <div className="product-line"></div>
      <div className="ordered-product">
        <p className="orderedproduct-youpaid">You paid</p>
        <p className="orderedproduct-price">₹208</p>
      </div>
    </div>
  );
}
