import React from "react";
import "../../../assets/1mobilecss/homepage/home1mobile.css";
import homeimage from "../../../assets/images/homemobile.svg";
import leaf1 from "../../../assets/images/leaf1.svg";

function Home1mobile() {
  return (
    <div className="home1mobile">
      <div style={{ background: "#371a0e", paddingBottom: "20px" }}>
        <img src={homeimage} alt="" className="home1mobile-image" />
        <div className="home1mobile-texts">
          <p className="home1mobile-heading">Heal your Skin.</p>
          <p className="home1mobile-caption">
            Nourish your Soul You are your Goal
          </p>
          <div className="home1mobile-button">
            <p>Shop All</p>
          </div>
        </div>
      </div>
      <div className="home1mobile-scroll">
        <p>SCROLL</p>
        <div className="home1mobile-line"></div>
      </div>
      <div className="home1mobile-background">
        <img src={leaf1} alt="" />
        <p>Experience the rejuvenating home-grown blends of Ayurveda.</p>
      </div>
    </div>
  );
}

export default Home1mobile;
