import React from "react";
import "../../assets/css/homepage/squareimage.css";

import babysquare from "../../assets/images/babysquare.svg";
import bodysquare from "../../assets/images/bodysquare.svg";
import facesquare from "../../assets/images/facesquare.svg";
import hairsquare from "../../assets/images/hairsquare.svg";
import mensquare from "../../assets/images/mensquare.svg";

function SquareImages() {
  return (
    <div className="squareimage">
      <div className="whatcare">
        <div className="whatcare-line">
          <p>What care are you looking for today?</p>
          <div className="line"></div>
        </div>
        <p className="caption">Have a look at our categories</p>
      </div>
      <div class="parent">
        <div class="image1">
          <div>
            <p>Body</p>
            <img src={bodysquare}></img>
          </div>
        </div>
        <div class="image2">
          <div>
            <p>Men's</p>
            <img className="img-fluid" src={mensquare}></img>
          </div>
        </div>
        <div class="image3">
          <div>
            <p>Face</p>
            <img src={facesquare}></img>
          </div>
        </div>
        <div class="image4">
          <div>
            <p>Hair</p>
            <img src={hairsquare}></img>
          </div>
        </div>
        <div class="image5">
          <div>
            <p>Baby</p>
            <img src={babysquare}></img>
          </div>
        </div>
      </div>
    </div>
  );
}

export default SquareImages;
