import React from 'react'
import NavBar from '../NavBar'
import contact from '../../assets/images/contactus.svg'
import '../../assets/css/contactus/contactus.css'
import FooterComponent from '../FooterComponent'

function Contactus() {
  return (
    <div>
      <NavBar></NavBar>
      <div className="contactus-main">
        <img src={contact} className="contactus-pic" alt="" />
        <p className="contactus-txt">Contact Us</p>
        <div className="contactus-frame">
          <div className="contactinfomain">
            <div className="contactinfo">
              <p>Contact Information</p>
            </div>
            <div className="contactdes">
              <p>
                Contact us at indiacare@ayurblends.com or leave a message below
                for all general inquiries.
              </p>
            </div>
            <div className="contactus-dropdown">
              <div>
                <select>
                  <option id="id1" value="none" selected disabled hidden>
                    First Name
                  </option>
                  <option id="id1" value="value1">
                    abcd
                  </option>
                  <option id="id1" value="value2">
                    aaa
                  </option>
                </select>
              </div>
              <div>
                <select>
                  <option value="none" selected disabled hidden>
                    Last Name
                  </option>
                  <option value="value1">eeee</option>
                  <option value="value2">aaaa</option>
                </select>
              </div>
            </div>
            <div>
              <input
                className="message"
                type="text"
                placeholder="Your Message"
              />
            </div>
            <div>
              <button className="sendbtn">Send</button>
            </div>
          </div>
        </div>
      </div>
      <FooterComponent></FooterComponent>
    </div>
  )
}

export default Contactus
