import React from 'react'
import "../../assets/css/payment/shippingtab.css"
import { useState } from 'react'

function Shippingtab() { 
    return (
        <div className='shipping-tab'>
            <p className='shipping-information'>Shipping Method &nbsp;&nbsp;&nbsp;</p>
            <div className='shippingtab-input-box'>
                <div className='standard-shipping payment-radio-input'>
                    <input  type="radio" value="standard shipping" name="gender" />
                    <p>&nbsp;&nbsp;Standard Shipping &nbsp; &nbsp; &nbsp; ₹0</p>
                </div>
                <div className='cash-on-delivery payment-radio-input'>
                    <input  type="radio" value="cash on delivery" name="gender" />
                    <p>&nbsp;&nbsp;Cash on Delivery &nbsp; &nbsp; &nbsp; ₹100</p>
                </div>
            </div>
        </div>
    )
}

export default Shippingtab