import React, { useState } from "react";
import "../../assets/css/ProductDetail/productdetail.css";
import NavBar from "../../Components/NavBar";
import FooterComponent from "../FooterComponent";
import YouMightAlsoLike from "../ProductList/YouMightAlsoLike";
import ProductDescription from "./ProductDescription";
import ProductImage from "./ProductImage";
import UseandBenefit from "./UseandBenefit";
import UserReviews from "./UserReviews";
import { useLocation } from "react-router-dom";


export default function ProductDetail() {
  
  const location = useLocation();
  const {id} = location.state;
  

  const productId = id
  return (
    <div className="productdetail-container">
      <NavBar />
      <div className="productdetail-page-path">
        <p className="productdetail-back">BACK</p>
        <p className="productdetail-path">Face &#62; Cleansers</p>
      </div>
      <div class="productdetail-parent">
        <div class="productdetail-div1">
          <ProductImage productId={productId}></ProductImage>
        </div>
        <div class="productdetail-div2">
          <ProductDescription productId={productId}></ProductDescription>
        </div>
      </div>
      <UseandBenefit></UseandBenefit>
      <UserReviews></UserReviews>
      <YouMightAlsoLike></YouMightAlsoLike>
      <FooterComponent></FooterComponent>
    </div>
  );
}
