import React from 'react'
import '../../../assets/1mobilecss/aboutusmobile/uniquesoul.css'
import unique from '../../../assets/images/uniquesoul.svg'

function Uniquesoulmob() {
  return (
    <div className="unimobmain"
      style={{
        backgroundColor: ' #F8F4ED',
        marginTop: '-20px',
        postion: 'relative',
        width:"100%",
      }}
    >
      <div className="unimob-main">
        <p>You are a unique soul</p>
        <img className="unimobpic" src={unique} />
        <div className="unimob-caption">
        <p >
          Just like our blends, we understand that each one of you is unique and
          deserve a different care. We are continuously moving to provide you
          just the care you need. We believe in diversifying our products so
          that you heal better everytime you choose the Ayur Blends Experience.
        </p>
        </div>
      </div>
    </div>
  )
}

export default Uniquesoulmob
