import React from "react";
import { useState, useEffect } from "react";
import "../../assets/css/ProductDetail/productdescription.css";
import plusellipse from "../../assets/images/plusellipse.svg";
import Rating from "@mui/material/Rating";
import Button from "@material-ui/core/Button";
import WooCommerce from "../../Api.js"


export default function ProductDescription(props) {
  const [productDetails, setProductDetails] = useState({});
  const [value, setValue] = React.useState(2);
  const [itemCount, setItemCount] = React.useState(1);
  const [isActive, setIsActive] = useState(false);
  const handleClick = () => {
    setIsActive((current) => !current);
  };

  // console.log(this.props.productId)
  const prId = props.productId



  useEffect(() => {
    fetchProductsDetails();
  }, []);

  let fetchProductsDetails = () => {
    WooCommerce
      .get(`products/${prId}`)
      .then((response) => {
        if (response) {
          setProductDetails(response.data);
          console.log("raj", response.data);
        }
      })
      .catch((error) => { });
  };

  return (
    <div className="productdes-container">
      <p className="productdes-title">{productDetails.name}</p>
      <p className="productdes-review-count">1401 Reviews</p>
      <div className="productdes-stars">

        <Rating
          sx={{
            "& .MuiRating-iconFilled": {
              color: "#3A383B",
            },
            "& .MuiRating-iconHover": {
              color: "brown",
            },
          }}
          value={value}
          onChange={(event, newValue) => {
            setValue(newValue);
          }}
        />
      </div>
      <p className="productdes-description">
        {productDetails.description}
      </p>
      <p className="productdes-price">₹ {productDetails.price}</p>
      <p className="productdes-offers">Buy 2, save 10%</p>
      <div className="productdes-line"></div>
      <p className="productdes-heading">SUITS</p>
      <p className="productdes-caption">Normal / Combination Skin</p>
      <div className="productdes-small-line"></div>
      <p className="productdes-heading">INGREDIENTS</p>
      <div className="productdes-add-ingredients">
        <p className="productdes-caption">Oatmeal . Almond . Fennel</p>
        <div>
          <img className="plusellipse" src={plusellipse}></img>
        </div>
      </div>
      <div className="productdes-small-line"></div>
      <p className="productdes-heading">QUANTITY</p>

      <div className="productdes-quantity">
        <div
          className="quantity-div1"
          style={{
            backgroundColor: isActive ? "#E3D9C6" : "",
            color: isActive ? "white" : "",
            cursor: "pointer",
          }}
          onClick={handleClick}
        >
          <p>30 ml </p>
        </div>
        <div
          className="quantity-div2"
          style={{
            backgroundColor: !isActive ? "#E3D9C6" : "",
            color: !isActive ? "white" : "",
            cursor: "pointer",
          }}
          onClick={handleClick}
        >
          <p>75 ml </p>
        </div>
      </div>
      <div className="productdes-buttons">
        <div className="productdes-cart-btn">
          <button>Add to cart - ₹ {productDetails.price}</button>
        </div>
        <div className="number-quantity">
          <Button
            onClick={() => {
              setItemCount(Math.max(itemCount - 1, 0));
            }}
          >
            <p className="itemcount-icon">-</p>
          </Button>
          <p className="product-itemcount">{itemCount}</p>
          <Button
            onClick={() => {
              setItemCount(itemCount + 1);
            }}
          >
            <p className="itemcount-icon">+</p>
          </Button>
        </div>
      </div>
    </div>
  );
}
