import React from "react";
import "../../assets/css/FAQ/faq.css";
import NavBar from "../../Components/NavBar";
import FooterComponent from "../FooterComponent";
import faqimage from "../../assets/images/faqimage.svg";
import { useState } from "react";

export default function FAQ() {
  const [open, setOPen] = useState("hidden");
  const toggle = () => {
    setOPen(open === "hidden" ? !"hidden" : "hidden");
  };
  return (
    <div>
      <NavBar></NavBar>
      <div className="faq-main">
        <div className="faqimg">
          <img src={faqimage} alt="ourstoryimg" />
          <div className="faq-title">Frequently Asked Questions</div>
        </div>
        <div className="faq-sub">
          <div>FAQS</div>
        </div>
        <div className="faqs">
          <div className="faq-line"></div>
          <div onClick={toggle}>
            <p className="faq-question">
              How do I create an Ayur Blends account for myself?
            </p>
          </div>
          <div className={open}>
            <p className="faq-answer">
              Fill in your details like your name, email address, mobile number.
              Add your address for all orders, set a strong password and done!
              You are registered with us.
            </p>
          </div>
        </div>
        <div className="faqs">
          <div className="faq-line"></div>
          <div onClick={toggle}>
            <p className="faq-question">
              I forgot my password. How do I log in?
            </p>
          </div>
          <div className={open}>
            <p className="faq-answer">
              Fill in your details like your name, email address, mobile number.
              Add your address for all orders, set a strong password and done!
              You are registered with us.
            </p>
          </div>
        </div>{" "}
        <div className="faqs">
          <div className="faq-line"></div>
          <div onClick={toggle}>
            <p className="faq-question">How do I place an order?</p>
          </div>
          <div className={open}>
            <p className="faq-answer">
              Fill in your details like your name, email address, mobile number.
              Add your address for all orders, set a strong password and done!
              You are registered with us.
            </p>
          </div>
        </div>{" "}
        <div className="faqs">
          <div className="faq-line"></div>
          <div onClick={toggle}>
            <p className="faq-question">How can I track my order?</p>
          </div>
          <div className={open}>
            <p className="faq-answer">
              Fill in your details like your name, email address, mobile number.
              Add your address for all orders, set a strong password and done!
              You are registered with us.
            </p>
          </div>
        </div>{" "}
        <div className="faqs">
          <div className="faq-line"></div>
          <div onClick={toggle}>
            <p className="faq-question">How do I cancel an order?</p>
          </div>
          <div className={open}>
            <p className="faq-answer">
              Fill in your details like your name, email address, mobile number.
              Add your address for all orders, set a strong password and done!
              You are registered with us.
            </p>
          </div>
        </div>
      </div>
      <FooterComponent></FooterComponent>
    </div>
  );
}
