import React from 'react'
import "../../assets/css/homepage/home2.css";
import roundtext from "../../assets/images/roundtext1.svg"
import butterfly from "../../assets/images/butterfly2.svg"
import bottle from "../../assets/images/bottleblack.svg"
import oatsimage from "../../assets/images/oatsimage.svg"
import { useNavigate } from "react-router";


function Home2() {
    const navigate = useNavigate()

    return (
        <div className='home2'>
            <div>
                <p className='caption1'>Cast your calming spell.</p>
                <p className='subcaption1'>Skin care essentials to elevate your mood.</p>
                <div className='b-images'>
                    <img className='butterflyimage' src={butterfly}></img>
                    <img className='butterflyimage1' src={butterfly}></img>
                    <img className='butterflyimage2' src={butterfly}></img>
                </div>
            </div>
                <div className='subcaption2'>Goodness of natural ingredients and love in every product</div>
            <div className='rightpanel'>
                <div>
                    <img className='roundtext' src={roundtext}></img>
                    <div className='boxborder'></div>
                    <img className='bottle' src={bottle}></img>
                </div>
                <div className='oats'>
                    <img src={oatsimage}></img>
                    <p>GOODNESS OF OATS</p>
                    <button onClick={() => {
              navigate("/ShopAll");
            }}>Check our Bestsellers</button>
                </div>
            </div>

        </div>
    )
}

export default Home2