import React, { useState } from 'react'
import Headers from '../NavBar'
// import routinepic from '../../assets/images/routineimg.svg'
import '../../assets/css/pickyourroutinepage/pickroutine.css'
import FooterComponent from '../FooterComponent'

function Pickyourroutinepage() {
  const [currentPage, setCurrentPage] = useState();

  const handleButtonClick = () => {
    setCurrentPage('about');
  }
  return (
    <div>
      <Headers></Headers>
      <div className='pick-main'>
        <div className="picktxt">Pick your Routine</div>
        <div className="nourish">Nourish Your Soul<br></br>You are your Goal</div>
        <div className='pick-frame-main'>
          <div className="pickframe">
            <div className="pick-question">
              <p className="pick-quesion">
              A daily ritual is a way of saying <br></br>I’m taking care of myself!
              </p>
            </div>
            <div>
              <button className="pickbutton" onClick={handleButtonClick}>Next</button>
            </div>
          </div>
        </div>
      </div>
      <FooterComponent></FooterComponent>
    </div>
  )
}

export default Pickyourroutinepage
