import React from 'react'
import { Routes, Route, BrowserRouter } from "react-router-dom";
import HomePage from './HomePage';
import Aboutus from './Aboutus/Aboutus';
import ProductList from './ProductList/ProductList';
import ProductnotFound from './Productnotfound/ProductnotFound';
import PickYourRoutine from './PickyourRoutine/Pickyourroutinepage';
import ProductDetail from '../Components/ProductDetail/ProductDetail';
import Contactus from '../Components/ContactUs/Contactus';
import Blog from '../Components/Blog/Blog';
import Payment from './Payment/Payment';
import OrderConfirmed from './Payment/OrderConfirmed';
import Profile from './myprofile/Profile';
import Body from './ProductList/Body';
import Hair from './ProductList/Hair';
import Face from './ProductList/Face';
import FAQ from './FAQ/FAQ';

export const PageRoutes = () => {
    return (
        <div>
            <BrowserRouter>
                <Routes>
                    <Route exact path='/' element={<HomePage />}></Route>
                    <Route exact path='/About' element={<Aboutus />}></Route>
                    <Route exact path='/ShopAll' element={<ProductList/>}></Route>
                    <Route exact path='/ProductNotFound' element={<ProductnotFound/>}></Route>
                    <Route exact path='/PickYourRoutine' element={<PickYourRoutine/>}></Route>
                    <Route exact path='/ProductDetail' element={<ProductDetail/>}></Route>  
                    <Route exact path='/Contactus' element={<Contactus/>}></Route>  
                    <Route exact path='/Blog' element={<Blog/>}></Route>  
                    <Route exact path='/Payment' element={<Payment/>}></Route>
                    <Route exact path='/OrderConfirmed' element={<OrderConfirmed/>}></Route>
                    <Route exact path='/myProfile' element={<Profile/>}></Route>
                    <Route exact path='/Body' element={<Body/>}></Route>
                    <Route exact path='/Hair' element={<Hair/>}></Route>
                    <Route exact path='/Face' element={<Face/>}></Route>
                    <Route exact path='/faq' element={<FAQ/>}></Route>
                </Routes>
            </BrowserRouter>
        </div>
    )
}
