import React, { useState } from "react";
import "../../assets/css/myprofile/profile.css";
import MyprofileTab from "./MyprofileTab";
import Headers from "../NavBar";
import MyOrderTab from "./MyOrderTab";
import WishlistTab from "./WishlistTab";
import myprofile from "../../assets/images/myprofile.svg";
import myorder from "../../assets/images/myorder.svg";
import mywishlist1 from "../../assets/images/mywishlist1.svg";
import logout1 from "../../assets/images/logout1.svg";
import myprofilevector from "../../assets/images/myprofilevector.svg";
import { useNavigate } from "react-router";

function Profile() {
  const navigate = useNavigate()

  const [myProfile, setMyProfile] = useState(true);
  const [myOrder, setMyOrder] = useState();
  const [wishlist, setWishlist] = useState();
  const [logout, setLogout] = useState();

  const [profileTab, setProfileTab] = useState("profile-tab");
  const [orderTab, setOrderTab] = useState("order-tab");
  const [wishlistTab, setWishlistTab] = useState("wishlist-tab");
  const [logoutTab, setLogoutTab] = useState("logout-tab");

  const handlemyprofile = () => (
    setMyProfile(true),
    setMyOrder(false),
    setWishlist(false),
    setLogoutTab(false),
    setProfileTab("profile-tab-active"),
    setOrderTab("order-tab"),
    setWishlistTab("wishlist-tab"),
    setLogoutTab("logout-tab")
  );

  const handlemyorder = () => (
    setMyProfile(false),
    setMyOrder(true),
    setWishlist(false),
    setLogoutTab(false),
    setProfileTab("profile-tab"),
    setOrderTab("order-tab-active"),
    setWishlistTab("wishlist-tab"),
    setLogoutTab("logout-tab")
  );

  const handlewishlist = () => (
    setMyProfile(false),
    setMyOrder(false),
    setWishlist(true),
    setLogoutTab(false),
    setProfileTab("profile-tab"),
    setOrderTab("order-tab"),
    setWishlistTab("wishlist-tab-active"),
    setLogoutTab("logout-tab")
  );

  const handllogout = () => (
    setMyProfile(false),
    setMyOrder(false),
    setWishlist(false),
    setLogoutTab(true),
    setProfileTab("profile-tab"),
    setOrderTab("order-tab"),
    setWishlistTab("wishlist-tab"),
    setLogoutTab("logout-tab-active")
  );

  return (
    <div>
      <Headers></Headers>
      <div class="profile-parent">
        <div class="profile-div1">
          <div className="myprofile-main">
            <div className="myprofile-pagetxt">
              <p>Home</p>
              <img src={myprofilevector} alt="" />
              <p className="myprofile-pagedes">Myprofile</p>
            </div>

            <div className="myprofile-sidebarmain">
              <div
                className={profileTab}
                onClick={() => {
                  handlemyprofile();
                }}
              >
                <img src={myprofile} alt="" />
                <p>My Profile</p>
              </div>

              <div
                className={orderTab}
                onClick={() => {
                  handlemyorder();
                }}
              >
                <img src={myorder} alt="" />
                <p>My Order</p>
              </div>

              <div
                className={wishlistTab}
                onClick={() => {
                  handlewishlist();
                }}
              >
                <img src={mywishlist1} alt="" />
                <p>Wishlist</p>
              </div>
              <div
                className={logoutTab}
                onClick={() => {
                  navigate("/")}}
              >
                <img src={logout1} alt="" />
                <p>Log Out</p>
              </div>
            </div>
          </div>
        </div>
        <div class="profile-div2">
          {myProfile ? <MyprofileTab></MyprofileTab> : null}

          {myOrder ? <MyOrderTab></MyOrderTab> : null}

          {wishlist ? <WishlistTab></WishlistTab> : null}
        </div>
      </div>
    </div>
  );
}

export default Profile;
