import React from "react";
import "../../assets/css/productnotfound/pagenotfound.css";
import Headers from "../NavBar";
import stone from "../../assets/images/stone.svg";
import YouMightAlsoLike from "../ProductList/YouMightAlsoLike";
import FooterComponent from "../FooterComponent";

function ProductnotFound() {
  return (
    <div style={{ backgroundColor: "rgba(255, 254, 243, 1)" }}>
      <Headers></Headers>
      <div className="product-not-found-main">
        <div className="not">Uh-Oh!</div>
        <div className="txt">
          <p>
            Looks like we are not yet formulated<br></br>this product. Please
            have patience!
          </p>
        </div>
        <div className="stone">
          <img src={stone} alt="" />
        </div>
        <div className="explo">
          <button className="btn">Explore other products</button>
        </div>
      </div>
      <YouMightAlsoLike></YouMightAlsoLike>
      <FooterComponent></FooterComponent>
    </div>
  );
}

export default ProductnotFound;
