import React from "react";
import "../../assets/css/homepage/shopbyconcerns.css";
import blemishes from "../../assets/images/blemishes.svg";
import unevenskin from "../../assets/images/unevenskin.svg";
import dryskin from "../../assets/images/dryskin.svg";
import frizzyhair from "../../assets/images/frizzyhair.svg";

export default function ShopByConcerns() {
  return (
    <div className="concernscontainer">
      <div className="concerns-main">
        <div className="concerns-title">
          <p>shop by concerns</p>
        </div>
        <div className="concerns-line"></div>
        <div className="concerns-caption">
          <p>Just the care you need</p>
        </div>
      </div>
      <div className="concerns">
        <div className="concerns-images">
          <div><img src={blemishes} alt="blemishes" />
            <p>Blemishes</p></div>
          <div><img src={unevenskin} alt="uneven skin" />
            <p>Uneven Skin</p></div>
          <div><img src={dryskin} alt="dry skin" />
            <p>Dry Skin</p></div>
          <div><img src={frizzyhair} alt="frizzy hair" />
            <p>Frizzy Hair</p></div>
        </div>
      </div>
    </div>
  );
}
