import React from 'react'
import '../../../assets/1mobilecss/aboutusmobile/formulatedmob.css'
import for1 from '../../../assets/images/formulated1.svg'
import for2 from '../../../assets/images/formulated2.svg'
import bottle from '../../../assets/images/formulattedbottle.svg'



function Formulateddmob() {
  return (
    <div className="formulatedmobmain" >

     <div className="formulatedmob-main"><p>Formulated just for you</p>
     <img className="formulatedmobpic2" src={for1}/>
     <img className="formulatedmobpic" src={for2}/>
     <div className="formulatedmob-caption">
     <p >Learning from her mother, Nagammal, who has with her an experience of 20 plus years in the field of self care, Aishwarya began formulating ayurvedic hair oils and masks. Seeing the results in only a short span of time, she started with branding and selling then products. While her products started getting loved and appreciated, she was doing her research on modern organic methods that could combine the traditional knowledge with the advanced methods used in the industry today.</p>
     </div>
     <div className="formulatedmob-bottle">
     <img className="formulatedmobbottle" src={bottle}/>
     <div className="bottlecaption">
     <p>Today, she is a certified formulator from the internationally recognised Formula Botanica in the UK. During this period of continuously experimenting and then improving her formulations, she has completed various other courses which guide her in obtaining herbal extracts for the organic beauty formulation.</p>
     </div>
     </div>
     </div>
    </div>
  )
}

export default  Formulateddmob


