import React from "react";
import Headers from "../NavBar";
import blog from "../../assets/images/blogpic.svg";
import hair from "../../assets/images/bloghairoilpic.svg";
import "../../assets/css/Blog/blog.css";
import FooterComponent from "../FooterComponent";

function Blog() {
  return (
    <div>
      <Headers></Headers>
      <div className="blog-main">
        <div className="blogpic">
          <img src={blog} alt="blog" />
          <p className="blogtxt">Blog</p>
        </div>
        <div className="blog-row">
          <div class="column">
            <img src={hair} alt="Snow" />
            <div>
              <p className="blog-cap1">PRODUCT | 15 Jan 2022</p>
              <div className="blog-line"></div>
              <p className="blog-cap2">Why is berry balm worth the hype ?</p>
              <p className="blog-cap3">
                We’ve all been told to drink OJ or take citric daily
                supplements, especially when our body needs some extra love,
                Vitamin C holds the crown for its unmatched healing powers, from
                the inside out
              </p>
            </div>
            <div className="blog-btn">
              <p className="readdmoretxt">Read More</p>
            </div>
          </div>
          <div class="column">
            <img src={hair} alt="Snow" />
            <div>
              <p className="blog-cap1">PRODUCT | 15 Jan 2022</p>
              <div className="blog-line"></div>
              <p className="blog-cap2">Why is berry balm worth the hype ?</p>
              <p className="blog-cap3">
                We’ve all been told to drink OJ or take citric daily
                supplements, especially when our body needs some extra love,
                Vitamin C holds the crown for its unmatched healing powers, from
                the inside out
              </p>
            </div>
            <div className="blog-btn">
              <p className="readdmoretxt">Read More</p>
            </div>
          </div>
          <div class="column">
            <img src={hair} alt="Snow" />
            <div>
              <p className="blog-cap1">PRODUCT | 15 Jan 2022</p>
              <div className="blog-line"></div>
              <p className="blog-cap2">Why is berry balm worth the hype ?</p>
              <p className="blog-cap3">
                We’ve all been told to drink OJ or take citric daily
                supplements, especially when our body needs some extra love,
                Vitamin C holds the crown for its unmatched healing powers, from
                the inside out
              </p>
            </div>
            <div className="blog-btn">
              <p className="readdmoretxt">Read More</p>
            </div>
          </div>
        </div>
        <div className="blog-loadmorebtn">
          <p>Load More</p>
        </div>
      </div>

      <FooterComponent></FooterComponent>
    </div>
  );
}

export default Blog;
