import React from 'react'
import "../assets/css/homepage.css";
import NavBar from './NavBar';
import FooterComponent from './FooterComponent';
import Home1 from './Homepage/Home1';
import Home2 from './Homepage/Home2';
import Fromthepress from './Homepage/Fromthepress';
import ShopByConcerns from './Homepage/ShopByConcerns';
import Blend from './Homepage/Blend';
import Certification from './Homepage/Certification';
import PickYourRoutine from './Homepage/PickYourRoutine';
import WinningInspirations from './Homepage/WinningInspirations';
import SquareImages from './Homepage/SquareImages';
import Testimonials from './Homepage/Testimonials';
import ShopTheFavourites from './Homepage/ShopTheFavourites';
import { useState, useEffect } from 'react';
import Home1mobile from './1MobileComponents/homepage/Home1mobile';
import Home2mobile from './1MobileComponents/homepage/Home2mobile';
import MobileConcerns from './1MobileComponents/homepage/MobileConcerns';
import MobileWhatCare from './1MobileComponents/homepage/MobileWhatCare';
import CertificationsMobile from './1MobileComponents/homepage/CertificationsMobile';
import { BrowserView, MobileView, isBrowser, isMobile } from 'react-device-detect';


function HomePage() {

  return (
    <div className='body'>
      <BrowserView>
        <NavBar></NavBar>
        <Home1></Home1>
        <Home2></Home2>
        <ShopTheFavourites></ShopTheFavourites>
        <ShopByConcerns></ShopByConcerns>
        <Blend></Blend>
        <SquareImages></SquareImages>
        <Certification></Certification>
        <PickYourRoutine></PickYourRoutine>
        <WinningInspirations></WinningInspirations>
        <Fromthepress></Fromthepress>
        <Testimonials></Testimonials>
        <FooterComponent></FooterComponent>
      </BrowserView>
      <MobileView>
        <Home1mobile></Home1mobile>
        <Home2mobile></Home2mobile>
        <MobileConcerns></MobileConcerns>
        <MobileWhatCare></MobileWhatCare>
        <CertificationsMobile></CertificationsMobile>
      </MobileView>

    </div>
  )
}
export default HomePage;