import React from "react";
import "../../../assets/1mobilecss/homepage/home2mobile.css";
import bottle from "../../../assets/images/bottleblack.svg";
import roundtext from "../../../assets/images/roundtext1.svg";
import oatsimage from "../../../assets/images/oatsimage.svg";
import bflymobile from "../../../assets/images/bflymobile.svg";

export default function Home2mobile() {
  return (
    <div className="home2mobile">
      <div className="home2mobile-title">
        <p className="home2mobile-caption1">Cast your calming spell.</p>
        <p className="home2mobile-subcaption1">
          Skin care essentials to elevate your mood.
        </p>
      </div>
      <div className="home2mobile-imagebox">
        <img className="home2mobile-roundtext" src={roundtext} alt="" />
        <img className="home2mobile-oatsimage" src={oatsimage} alt="" />
        <div className="home2mobile-dottedline"></div>
        <img className="home2mobile-bottle" src={bottle} alt="" />
        <div className="home2mobile-butterfly-images">
          <img className="home2mobile-butterflyimage2" src={bflymobile}></img>
          <img className="home2mobile-butterflyimage1" src={bflymobile}></img>
        </div>
      </div>
      <div className="home2mobile-button">
        <p>Bestsellers</p>
      </div>
    </div>
  );
}
