import React from "react";
import "../../assets/css/About/rawbio2.css";
import rawbioimg2 from "../../assets/images/rawbioimg2.svg";

export default function RawBiology2() {
  return (
    <div className="bio2-main">
      <div className="bio2-title">
        <p>Raw Biology</p>
      </div>
      <div className="bio2-container">
        <div className="bio2-caption">
          <p>
            Back in 2019, our first customers were only our friends and
            relatives to whom we regularly distributed our products for reviews
            before handing them out to you so that you can love, care and pamper
            yourself free from all toxicity. It is with their valuable feedback
            and your trust that Ayur Blends has been able to improve and show
            the best results.
          </p>
        </div>
        <div className="bio2-image">
          <img src={rawbioimg2} alt="rawbioimg2" />
        </div>
      </div>
    </div>
  );
}
