import React from "react";
import "../../assets/css/homepage/certification.css";
import organic from "../../assets/images/organic.svg";
import animals from "../../assets/images/animals.svg";
import cruelyfree from "../../assets/images/cruelyfree.svg";
import natural from "../../assets/images/natural.svg";

function Certification() {
  return (
    <div className="certification-container">
      <div className="certification-title">Our Certifications</div>
      <div className="frame">
        <div className="vectors">
          <div>
            <img src={organic} alt="organic" />
            <p>Natural Plant Based<br></br>Formulations</p>
          </div>
          <div className="not-tested">
            <img src={animals} alt="not tested on animals" />
            <p>No toxic chemicals</p>
          </div>
          <div>
            <img src={natural} alt="100% natural" />
            <p>No harmful side effects</p>
          </div>
          <div className="cruely-free">
            <img src={cruelyfree} alt="cruelty free" />
            <p>All natural care</p>
          </div>
        </div>
      </div>
    </div>
  );
}

export default Certification;
