import React from "react";
import "../../assets/css/About/ourstory.css";
import ourstoryimg from "../../assets/images/ourstory.svg";
import ourstoryleaf from "../../assets/images/ourstoryleaf.svg";

function Ourstory() {
  return (
    <div>
      <div className="maindiv">
        <div className="storyimg">
       
            <img src={ourstoryimg} alt="ourstoryimg" />
        
          <div className="oursto">Our Story</div>
        </div>
        <div className="ourstorydiv">
          <div className="leaf">
            <img src={ourstoryleaf} alt="ourstoryleaf" />
          </div>
          <div className="divour">
            <div className="exp">
              <p>Experience the rejuvenating home-grown blends of Ayurveda.</p>
            </div>
            <div className="uncom">
              <p> Uncompromising care.<br></br>
                Each Ayur Blends product is clinically proven to leave you with
                no harmful side effects.
              </p>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}

export default Ourstory;
