import React from "react";
import "../../../assets/1mobilecss/homepage/mobilewhatcare.css";
import babymobileimage from "../../../assets/images/babymobileimage.svg";
import bodymobileimage from "../../../assets/images/bodymobileimage.svg";
import facemobileimage from "../../../assets/images/facemobileimage.svg";
import hairmobileimage from "../../../assets/images/hairmobileimage.svg";
import mensmobileimage from "../../../assets/images/mensmobileimage.svg";
import bflymobile from "../../../assets/images/bflymobile.svg";

export default function () {
  return (
    <div className="m-care">
      <p className="m-care-title">What care are you looking for today?</p>
      <p className="m-care-caption">Have a look at our categories</p>
      <div className="m-care-bimages">
        <img className="m-care-butterflyimage1" src={bflymobile}></img>
        <img className="m-care-butterflyimage2" src={bflymobile}></img>
        <img className="m-care-butterflyimage3" src={bflymobile}></img>
      </div>
      <div class="m-care-image1">
        <p>Body</p>
        <img src={bodymobileimage}></img>
      </div>
      <div class="m-care-image2">
        <div>
          <p>Face</p>
          <img src={facemobileimage}></img>
        </div>
      </div>
      <div class="m-care-image3">
        <div>
          <p>Men's</p>
          <img src={mensmobileimage}></img>
        </div>
      </div>
      <div class="m-care-image4">
        <div>
          <p>Hair</p>
          <img src={hairmobileimage}></img>
        </div>
      </div>
      <div class="m-care-image5">
        <div>
          <p>Baby</p>
          <img src={babymobileimage}></img>
        </div>
      </div>
      <div style={{ display: "flex", flexDirection: "row", paddingBottom: 10, paddingTop: 10 }}>
        <div className="m-care-smallbox"></div>
        <p className="m-care-shopnow">SHOP NOW</p>
      </div>
      <div className="m-care-line"></div>
    </div>
  );
}
