import React from "react";
import productthumb from "../../assets/images/sidecartproduct.svg";
import close from "../../assets/images/close-x.svg";
import plusellipse from "../../assets/images/plusellipse.svg";
import "../../assets/css/payment/sidecart.css";
import Button from "@material-ui/core/Button";

function SideCart() {
  const [cartItemCount, setCartItemCount] = React.useState(1);

  return (
    <div className="payment-sidecart">
      <div className="cart-detail">
        <div>
          <img src={productthumb}></img>
        </div>
        <div className="product" style={{ marginLeft: 30 }}>
          <div className="productname">
            <p>ACT Face Cleanser | Dry skin</p>
            <img src={close}></img>
          </div>
          <div className="productoffer">Add one more to get 10% off</div>
          <div className="sidecart-number-quantity">
            <Button
              onClick={() => {
                setCartItemCount(Math.max(cartItemCount - 1, 0));
              }}
            >
              <p className="itemcount-icon">-</p>
            </Button>
            <p className="product-itemcount">{cartItemCount}</p>
            <Button
              onClick={() => {
                setCartItemCount(cartItemCount + 1);
              }}
            >
              <p className="itemcount-icon">+</p>
            </Button>
          </div>
          <div className="side-cart-price">₹299</div>
        </div>
      </div>
      <div className="sidecart-line"></div>
      <div className="sidecart-promo">
        <p>Apply a promotional code</p>
        <img src={plusellipse}></img>
      </div>
      <div className="sidecart-line"></div>
      <div className="sidecart-details-gift">
        <p>Make it a gift!</p>
        <a href="/">VIEW OPTIONS</a>
      </div>

      <div className="sidecart-details">
        <p>SUBTOTAL</p>
        <p className="side-cart-price">₹208</p>
      </div>

      <div className="sidecart-details">
        <p>SHIPPING COST</p>
        <p>₹0</p>
      </div>
      <div className="sidecart-line"></div>
      <div className="sidecart-total">
        <p>Total</p>
        <p className="sidecart-total-amount">₹208</p>
      </div>
    </div>
  );
}

export default SideCart;
