import React from "react";
import Ourstory from "./Ourstory";
import NavBar from "../NavBar.js";
import Founders from "../Aboutus/Founders.js";
import Rawbiology from "../Aboutus/Rawbiology.js";
import RawBiology2 from "./RawBiology2";
import Howstarted from "./Howstarted";
import FooterComponent from "../FooterComponent";
import { useState, useEffect } from 'react';
import Ourstorymobile from '../1MobileComponents/aboutus/Ourstorymobile';
import Foundersmob from '../1MobileComponents/aboutus/Foundersmob';
import Howwestartedmob from '../1MobileComponents/aboutus/Howwestartedmob';
import Healnourishmob from '../1MobileComponents/aboutus/Healnourishmob';
import Formulatedmob from '../1MobileComponents/aboutus/Formulatedmob';
import Uniquesoulmob from '../1MobileComponents/aboutus/Uniquesoulmob';
import Theworldisnoticingmob from '../1MobileComponents/aboutus/Theworldisnoticingmob';




function Aboutus() {
  const [isMobile, setIsMobile] = useState();
  useEffect(() => {
    const handleResize = () => {
      setIsMobile(window.innerWidth <= 500);
    };
    window.addEventListener('resize', handleResize);
    handleResize();
    return () => window.removeEventListener('resize', handleResize);
  }, []);
  return (
    <div>
       {isMobile ? (
          <div >
            <Ourstorymobile></Ourstorymobile>
             <Foundersmob></Foundersmob> 
             <Howwestartedmob></Howwestartedmob>
            <Healnourishmob></Healnourishmob>
            <Formulatedmob></Formulatedmob>
            <Uniquesoulmob></Uniquesoulmob>
            <Theworldisnoticingmob></Theworldisnoticingmob>    



          </div>
         
        ) : <>
      <NavBar></NavBar>
      <Ourstory></Ourstory>
      <Founders></Founders>
      <Howstarted></Howstarted>
      <Rawbiology></Rawbiology>
      <RawBiology2></RawBiology2>
      <FooterComponent></FooterComponent>
      </>
       }
    </div>
  );
}

export default Aboutus;
